import { Injectable } from '@angular/core';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpClient,HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { User } from '../model/user.model';
import { Post } from '../model/post.model';
import { TextPost } from '../model/textPost.model';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  baseUrl =  "https://jsonplaceholder.typicode.com/users";

  constructor(private http: HttpClient) {
    console.log('rest service constructor called');
  }

 
 

  public getMethod(url: string): Observable<any>{
    return this.http.get(url);
  }

  public putMethod(url:string,id:string): Observable<any>{
    let param1 = new HttpParams().set('userId',id);
    return this.http.get(url,{params: param1});
  }

  public postMethod(url:string, post:Post): Observable<any>{
    return this.http.post(url,post);
}
public post2Method(url:string, textToPost:TextPost): Observable<any>{
  console.log("hereW_________________");
  return this.http.post(url,textToPost);
}


  

getMethod1(ip, options) {
  console.log('options is: ', options);
  options['observe'] = 'response';
  return this.http.get(ip, options);
}
postMethod3(ip, body, options) {
  options['observe'] = 'response';
  return this.http.post(ip, JSON.stringify(body), options).pipe(
    map((response) => {
      
      return response;
    }),
    catchError(this.handleError('post request for ' + ip))
  );
}
getMethod3(ip, options) {
  //this.loading = true;
  options['observe'] = 'response';
  options['responseType'] = 'json';
  return this.http.get(ip, options).pipe(
    map((response) => {
      //this.loading = false;
      console.log('response is: ', response);
      
      return response;
    }),
    catchError(this.handleError('get request for ' + ip))
  );
}

deleteMethod3(ip, options) {
  options['observe'] = 'response';
  options['responseType'] = 'json';
  options.headers = this.getHeaders();
  return this.http.delete(ip, options).pipe(
    map((response) => {
      console.log('response is: ', response);
      
      
      return response;
    }),
    catchError(this.handleError('delete request for ' + ip))
  );
}



getHeaders(): HttpHeaders {
  let header: HttpHeaders = new HttpHeaders(
    {
      'Content-Type': 'application/json',
    });
  return header;
}


/**
 * Handle Http operation that failed.
 * Let the app continue.
 * @param operation - name of the operation that failed
 * @param result - optional value to return as the observable result
 */
private handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {
    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instea
    // Let the app keep running by returning an empty result.
    return of(result as T);
  };
}

  
}
