import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import { HttpClient,HttpHeaders, HttpErrorResponse } from '@angular/common/http';

import { User } from '../model/user.model';
import { Post } from '../model/post.model';
import { TextPost } from '../model/textPost.model';
import { SearchResult } from '../model/searchResult.model';


@Injectable({
  providedIn: 'root'
})
export class SearchService {
  userList: User[];
  searchList: SearchResult[];
  postToPost = new Post();
  postGot : Post;
  searchUrl = "https://jsonplaceholder.typicode.com/users";
  postUrl= "https://jsonplaceholder.typicode.com/posts";
  postTextURL="http://localhost:8080/main/fullSearch";
  textToPost = new TextPost();
  responseString:string;
  //http://localhost:8080/main/fullsearch POST string
  constructor(private rest: HttpService) {
   }

  getUserList() {    
    return this.rest.getMethod(this.searchUrl).subscribe(data => {
      this.userList = data;
    });
  }

  searchText(text : string){
    this.textToPost.text =  text;
    console.log("inside searchText(text) ...text = " + text);
        console.log("in url ... = " + this.postTextURL);

    this.rest.post2Method(this.postTextURL,this.textToPost).subscribe(data =>{
      console.log("here");
      //this.responseString = data;
      console.log(data);
      //console.log(this.responseString);
      this.searchList = data;
    });

  }

  searchText3(text : string){
    this.textToPost.text =  text;
    console.log("inside searchText(text) ...text = " + text);
        console.log("in url ... = " + this.postTextURL);

    this.rest.postMethod3(this.postTextURL,this.textToPost,{ headers: this.getHeaders() }).subscribe(data =>{
      console.log("here");
      //this.responseString = data;
      console.log(data);
      //console.log(this.responseString);
    });

  }

  postPost(){
    this.postToPost.body="test";
    this.postToPost.title="testTitle";
    this.postToPost.userId=5;

    this.rest.postMethod(this.postUrl,this.postToPost).subscribe(data => {
      this.postGot = data;
      console.log(this.postGot.body);
    });
  }

  getHeaders(): HttpHeaders {
    let header: HttpHeaders = new HttpHeaders(
      {
        'Content-Type': 'application/json'
      });
    return header;
  }
  
}
